$templateCache.put('app/controllers/home/home.html','<post-it text1="\'I am passionate about development in Python\'"></post-it>\n\n<post-it text1="\'I loved to be a team lead at Virtus and work with such diverse backgrounds.\'"></post-it>\n\n<post-it text1="\'I am in love with Germany since my exchange in Dresden, Saxony.\'" text2="\'PS: I &hearts; Spekulatius\'">\n</post-it>\n\n<article class="biography">\n  <div class="layout">\n    <h1>Biography</h1>\n    <p>\n      The achievement I am proudest of is certainly the speed I grew in my past job. I soon became of the Senior developers and started leading teams (one at a time) with very different backgrounds. Don\'t take me wrong: I love coding (check out my github), but I am passionate about leading and connecting. Today, my boldness and confidence on my capabilities/ethics are my main strengths. I am not afraid of starting on a very initial level at an ambitious company (if I think they have a good service), because I am confident that I will grow my career there (respecting hierarchy and being truthful to my colleagues). As soon as I finished my 3-year contract I went back to Germany to find my next professional challenge.\n    </p>\n\n    <p>\n      I have a bachelor in Computer Science, I joined research projects in Brazil and Germany and I read books on innovation, leadership and biographies on IT personalities (such as Alan Turing, Steve Jobs and Bill Gates), so I am looking for a work I can be proud of. I don\'t want to merely build software, I want to invent the future.\n    </p>\n\n    <p>\n      My profile is aligned to 3 main categories (in order of preference): Data Science, Backend/API and Frontend. I am passionate about two technologies and have wide experience with them: Python (for Data Science and Backend) and SQL/NoSQL, but I worked with many, many others (e.g, Java, R, ASP.Net, so forth) and I am always excited to learn new frameworks/technologies.\n    </p>\n\n    <p>\n      Prefered industries: IT, Financial and Environmental.\n    </p>\n  </div>\n</article>\n\n<ul class="lined">\n  <li id="work-petitcode">\n    <div class="centered"><div class="flag-de"></div></div>\n    <article class="experience">\n      <h1>Python Developer at Petit Code</h1>\n      <p>\n        Temporary job as Python Developer.<br>\n        Local: Betahaus\n      </p>\n    </article>\n  </li>\n\n  <li id="work-virtus">\n    <div class="centered"><div class="flag-br"></div></div>\n    <article class="experience">\n      <h1>Software Engineer at Virtus</h1>\n      <p>\n        Full-Time Contract: 3 years.<br>\n        Local: Campina Grande\n      </p>\n    </article>\n  </li>\n\n  <li id="bachelor-ufcg">\n    <div class="centered"><div class="flag-br"></div></div>\n    <article class="experience">\n      <h1>Bachelor in Computer Science</h1>\n      <p>\n        2010 ~ 2015<br>\n        Local: Universidade Federal de Campina Grande\n      </p>\n    </article>\n  </li>\n\n  <li id="research-tud">\n    <div class="centered"><div class="flag-de"></div></div>\n    <article class="experience">\n      <h1>Research Project on Smart Grids</h1>\n      <p>\n        2013 ~ 2014<br>\n        Local: Technische Universit\xE4t Dresden\n      </p>\n    </article>\n  </li>\n\n  <li id="research-lsd">\n    <div class="centered"><div class="flag-br"></div></div>\n    <article class="experience">\n      <h1>Research Project on Smart Grids</h1>\n      <p>\n        2012 ~ 2013<br>\n        Local: Universidade Federal de Campina Grande\n      </p>\n    </article>\n  </li>\n\n  <li id="research-splab">\n    <div class="centered"><div class="flag-br"></div></div>\n    <article class="experience">\n      <h1>Research Project on Software Testing</h1>\n      <p>\n        2011 ~ 2012<br>\n        Local: Universidade Federal de Campina Grande\n      </p>\n    </article>\n  </li>\n</ul>\n');
$templateCache.put('app/controllers/lsd/lsd.html','<article class="academic-experience" id="academic-ufcg"><div class="container">\n  <h1 class="university">LSD - Laborat\xF3rio de Sistemas Distribu\xEDdos: dec/2012 - sep/2013</h1>\n  <section class="university-intro">\n    <p>\n      <a target="_blank" href="https://www.lsd.ufcg.edu.br/#/">LSD</a> (Distributed Systems Laboratory) is a lab where UFCG researchers collaborate on Distributed Systems Engineering projects. It was here that\n      <a target="_blank" href="http://ourgrid.lsd.ufcg.edu.br/overview.php">OurGrid</a> was born.\n    </p>\n    <p>\n      My project was on Smart Grids and my supervisor was Dr.-Ing. Andrey El\xEDsio Monteiro Brito (graduated at Technische Universit\xE4t Dresden). He introduced me to Distributed Systems, working on cloud services and the concept of Smart Grids. We built an open source tool to create simulated data for our research, which was continued on the project at TU Dresden.\n    </p>\n  </section>\n\n  Organization of Events:\n  <ul>\n    <li>\n      TruEGrid - Trustworthyness and Energy-Efficient Smart Grids 2013, Campina Grande, UFCG. June, 2013.\n    </li>\n  </ul>\n\n  Participation of Events:\n  <ul>\n    <li>\n      TruEGrid: 2013.\n    </li>\n  </ul>\n\n</div></article>\n');
$templateCache.put('app/controllers/personal/interests.html','<article><div class="container">\n  <section class="literature">\n    <h1>Literature</h1>\n    <p>\n      Reading is my other true passion.\n      I have books on three categories: History of IT, Science and Arts and biographies of people in these fields (Alan Turing\'s biography by his mother Sara Turing; "Steve Jobs" by Walter Isaacson; etc.); Finances, Leadership and Building/Directing a Company ("Creativity, Inc." by Edwin Catmull, president of Pixar); Classics of Literature and Poetry (Oscar Wilde\'s work, Clarice Lispector, etc); and novels by new contemporary writers (such as "Aristotle and Dante Discover the Secrets of the Universe" novel by Benjamin Alire S\xE1enz; "O Punho Fechado no Fio da Navalha" poetry book by Patricia Naia).\n    </p>\n\n    <section>\n      <h2>Oscar Wilde</h2>\n      <p>\n        I am a <b>HUGE</b> fan of Oscar Wilde and his novel "The Picture of Dorian Gray" is certainly my favorite book since 2010. I watched many movies based on his works and life. I have a total of 14 books on my collection of Oscar Wilde: 8 different editions of "The Picture of Dorian Gray" (one of them also includes many of his plays and tales:\n        <a target="_blank" href="https://www.amazon.com/Picture-Dorian-Gray-Other-Works/dp/1435139437/ref=sr_1_1?s=books&ie=UTF8&qid=1523480945&sr=1-1&keywords=9781435139435">ISBN 9781435139435</a>); 2 editions with "De Profundis" and "The Ballad of Reading Gaol"; 2 books of Epigrams; 1 edition of "The ghost of the canterville" (it is also included on <a target="_blank" href="https://www.amazon.com/Picture-Dorian-Gray-Other-Works/dp/1435139437/ref=sr_1_1?s=books&ie=UTF8&qid=1523480945&sr=1-1&keywords=9781435139435">ISBN 9781435139435</a>); and 1 edition of "Teleny or The Reverse of the Medal" (which is attributed to him and, despite of all controversy, I strongly believe it is his).\n      </p>\n      <div id="collection-wilde" class="article-image"></div>\n    </section>\n\n    <section>\n      <h2>Poetry</h2>\n      <p>My other favorite writer is the Poet Fernando Pessoa (from Portugal) and his heteronyms (Alberto Caeiro, \xC1lvaro de Campos, Bernardo Soares, etc.)</p>\n    </section>\n  </section>\n</div></article>\n');
$templateCache.put('app/controllers/petitcode/petitCode.html','<article class="work-experience" id="work-petitcode"><div class="container">\n  <h1 class="workplace">PetitCode (freelancer)</h1>\n  <section class="workplace-intro">\n    <p>\n      PetitCode is a freelancer-based German startup with 8 employees.\n      We deliver IT services to other companies here in Berlin and around Germany.\n    </p>\n    <p>\n      PetitCode is my only experience with freelancer projects.\n      Most of our clients contract us to build their new website for a beautiful online presence.\n    </p>\n  </section>\n  <section class="work-position">\n    <p>\n      I was contracted as the house\'s Python and Angular Developer, but in my job at PetitCode I love to help less experienced engineers to build and manage their solution on AWS (e.g, <a href="https://app.findsomebuddy.de/reactapp/" target="_blank">Alex\'s FindSomebuddy</a>) and with any questions on git, GitLab, GitHub Pages, Jenkins, etc. Technologies: NGINX and Apache, Linux, Django.\n    </p>\n  </section>\n  <div class="team-picture"></div>\n  <section>\n    <h2>Key Responsibilities: </h2>\n    <ul>\n      <li>\n        Build or give consultation on Django solutions already in production;\n      </li>\n      <li>\n        Deployment to production AWS servers (setup with NGINX).\n      </li>\n    </ul>\n  </section>\n</div></article>\n');
