$templateCache.put('app/controllers/splab/splab.html','<article class="academic-experience" id="academic-ufcg"><div class="container">\n  <h1 class="university">SPLab - Software Practices: jun/2011 - dec/2012</h1>\n  <section class="university-intro">\n    <p>\n       Software Practices Laboratory is a lab where UFCG researchers collaborate on Software Engineering projects.\n    </p>\n    <p>\n      I worked on Software Testing Engineering with PhD Patricia Duarte de Lima Machado (graduated at University of Edinburgh, UK) and Dr. Wilkerson de Lucena Andrade (graduated at Universidade Federal de Campina Grande).\n    </p>\n  </section>\n\n  Presentations, Publications and Awards:\n  <ul>\n    <li>\n      [<a target="_self" href="assets/download/publications/sast_2012.pdf" download>Paper</a> + <a target="_blank" href="http://splab.computacao.ufcg.edu.br/publications/journal-and-conference-papers">Presentation</a>] ANDRADE, Joeffison S. Automating Test Case Creation and Execution for Embedded Real-time Systems. 6th Brazilian Workshop on Systematic and Automated Software Testing - Sociedade Brasileira de Computa\xE7\xE3o. Sep 23, 2012.\n      <br>Available from page 53 to 62: <a target="_self" href="assets/download/publications/sast_2012.pdf" download>Conference papers</a>.\n    </li>\n  </ul>\n\n  Participation of Events:\n  <ul>\n    <li>\n      6th Brazilian Workshop on Systematic and Automated Software Testing: 2012\n    </li>\n  </ul>\n  <div id="realtimepco"></div>\n  <div id="realtimepco-diagram"></div>\n</div></article>\n');
